.modal-main {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1000;
  }
  
.modal-response {
  position:fixed;
  background: white;
  width: 80%;
  height: 50vh;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  z-index: 2000;
  padding:3%;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}

.modal-content {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  border: none;
}

.button-close {
  position: absolute;
  top: 2em;
  right: 2em;
  border: none;
  background-color: transparent;
}
.home {
    border: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-image: url(/static/media/moving-box.9bd71fac.jpg);
    background-size: cover;
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
    border-radius: 0;
}
.buttons-home {
    border: 0;
    height: 40vh;
    width: 50%;
    display: flex;
    justify-content: center;
    max-width: 500px;
    flex-direction: column;
    align-items: center;
    padding: 5%;
    margin-bottom: -5%;
}

.title-home {
    text-align: center;
    font-size: 50px;
    color: white;
    width: auto;
    height: auto;
    margin-top: 6%;
}


@media (max-width: 725px) {
    .home {
        background-image: none;
    }

     .buttons-home {
        width: 100%;
        margin-bottom: 25%;
    }

    .title-home {
        width: 80%;
        margin-top: 20%;
    }
}
